import {
	appuserPages,
	appuserAdd,
	appuserUpdate,
	appuserDelete,
	//appuserDetail
} from 'api/api';
import userManagerform from './appComponents/userManagerForm';
let pageData = {
	avatar: '',
	cfType: '',
	createTime: '',
	creator: '',
	deviceCode: '',
	deviceType: '',
	email: '',
	gender: '',
	modifyTime: '',
	modifyor: '',
	nikeName: '',
	passportNo: '',
	phone: '',
	points: '',
	realName: '',
	statue: '',
	telPrefix: '',
	userCode: '',
	userCountry: '',
	userId: '',
	userName: '',
	userPwd: '',
	userSalt: '',
}
export default {
	data() {
		return {
			searchValue: '',
			tableData: {},
			dialogVisible: false,
			dialogTyle: '',
			pageData: {
				...pageData
			},
			page: {
				start: 0,
				length: 10,
				draw: 1,
			},
			currentPage: 1,
			pageSizes: [10, 20, 30],
		}
	},
	components: {
		userManagerform
	},
	created() {
		this.init()
	},
	watch: {
    searchValue(value){
        if(!value){
          appuserPages(this.page).then(res => {
				this.tableData = res.data.obj
			})
        }
      }
    },
	methods: {
		init() {
			appuserPages(this.page).then(res => {
				this.tableData = res.data.obj
			})
		},
		onQuery() {
			if(this.searchValue){
	           appuserPages({...this.page,userName:this.searchValue}).then(res => {
				this.tableData = res.data.obj
				})
	        }
		},
		showDilog(type, obj=pageData) {
			this.dialogVisible = true
			this.dialogTyle = type
			this.pageData = { ...obj }
		},
		onSubmit() {
			this.$refs.userManager.validate(valid => valid && this.commitForm())
		},
		commitForm() {
			let formData = new FormData();
			for(let key in this.pageData) {
				if(key == 'avatar') {
					formData.append('icon', this.pageData.avatar)// this.bootPageObj[key]
				} else {
					formData.append(key, this.pageData[key]);
				}
			}
			if(this.dialogTyle === 'edit') {

				appuserUpdate(formData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			} else if(this.dialogTyle === 'add') {
				appuserAdd(formData).then(res => {
					this.init()
					this.dialogVisible = false
				})
			}
		},
		handleDelete(obj) {
			this.$confirm(this.$t('commom.deleConfim'), '', {
				confirmButtonText: this.$t('commom.sure'),
				cancelButtonText: this.$t('commom.cancel'),
				type: 'warning'
			}).then(() => {
				appuserDelete({
					userId: obj.userId
				}).then(res => {
					this.init()
				})
			}).catch(res => {

			})
		},
		getpageData(){
	       	appuserPages({...this.page,userName:this.searchValue}).then(res => {
				this.tableData = res.data.obj
			})
        },
		handleSizeChange(val) {
			this.page.length = val
			this.page.start = (this.currentPage - 1) * this.page.length
	        	this.getpageData()
		},
		handleCurrentChange(val) {
			this.currentPage = val;
			this.page.start = (this.currentPage - 1) * this.page.length
			setTimeout(()=>{
	        	this.getpageData()
	        },0)
		},
		

	}
}



